import React, {useMemo} from 'react';
import { useHistory } from 'react-router-dom';

export default function Basket(props) {
  const history = useHistory();
  const { cartItems, onAdd, onRemove } = props;
  const itemsPrice = cartItems.reduce((a, c) => a + c.qty * c.price, 0);
  const taxPrice = itemsPrice * 0.14;
  const shippingPrice = itemsPrice > 2000 ? 0 : 20;

  // const totalPrice = () => {
  //   console.log("I am getting called");
  //   return itemsPrice + taxPrice + shippingPrice;
  // }
  
  // no re-rendering of the function will happen on action of other events.
  const totalPrice = useMemo(() => {
    console.log("useMEMO");
    return itemsPrice + taxPrice + shippingPrice
  }, [itemsPrice,taxPrice,shippingPrice]);
   const handleClick = () => {
     let confirm = window.confirm("Are you sure you want to place the orders");
     if(confirm === true) {
     history.push('/ordered');
     }
   }
  return (
    <aside className="block col-1">
      <h2>Cart Items</h2>
      <div>
        {cartItems.length === 0 && <div>Cart is empty</div>}
        {cartItems.map((item) => (
          <div key={item.id} className="row">
            <div className="col-2">{item.name}</div>
            <div className="col-2">
              <button onClick={() => onRemove(item)} className="remove">
                -
              </button>{' '}
              <button onClick={() => onAdd(item)} className="add">
                +
              </button>
            </div>

            <div className="col-2 text-right">
              {item.qty} x â‚¹{item.price.toFixed(2)}
            </div>
          </div>
        ))}

        {cartItems.length !== 0 && (
          <>
            <hr></hr>
            <div className="row">
              <div className="col-2">Items Price</div>
              <div className="col-1 text-right">â‚¹{itemsPrice.toFixed(2)}</div>
            </div>
            <div className="row">
              <div className="col-2">Tax Price</div>
              <div className="col-1 text-right">â‚¹{taxPrice.toFixed(2)}</div>
            </div>
            <div className="row">
              <div className="col-2">Shipping Price</div>
              <div className="col-1 text-right">
                â‚¹{shippingPrice.toFixed(2)}
              </div>
            </div>

            <div className="row">
              <div className="col-2">
                <strong>Total Price</strong>
              </div>
              <div className="col-1 text-right">
                <strong>â‚¹{totalPrice.toFixed(2)}</strong>
              </div>
            </div>
            <hr />
            <div className="row">
                <button onClick={() => handleClick()}>Place Order</button>
            </div>
          </>
        )}
      </div>
    </aside>
  );
}